body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #eaf3f8;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Segoe UI", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  width: 50%;
  margin: 0 auto;
  padding-top: 0.5rem;
  height: 100vh;
}

.logo {
  text-align: center;
  font-family: "Roboto";
}

.logo img {
  width: 150px;
  margin: 0;
}

.logo h3 {
  color: #218dfd;
  margin: 0;
  font-family: "Roboto";
}

.forminput {
  font-family: "Roboto";
}

.logo p {
  font-family: fantasy;
}

.todo {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

input:focus {
  outline: none;
}

.btn {
  border: none;
  margin: 0 0.2rem;
  width: 5rem;
  height: 1.5rem;
  color: white;
  font-weight: bold;
}

.btn-green {
  background: #00a44e;
}

.btn-red {
  background: #ff2060;
}

.btn-add {
  background: #089c99;
}

.btn-blue {
  background: #218dfd;
}

.btn-green button {
  border: none;
  margin: 0 0.2rem;
  width: 5rem;
  height: 1.5rem;
  color: white;
  font-weight: bold;
  background: #00a44e;
}

.btn-red button {
  border: none;
  margin: 0 0.2rem;
  width: 5rem;
  height: 1.5rem;
  color: white;
  font-weight: bold;
  background: #ff2060;
}

.btn-blue button {
  border: none;
  margin: 0 0.2rem;
  width: 5rem;
  height: 1.5rem;
  color: white;
  font-weight: bold;
  background: #218dfd;
}

.btn-add button {
  border: none;
  margin: 0 0.2rem;
  width: 5rem;
  height: 1.5rem;
  color: white;
  font-weight: bold;
  background: #089c99;
}

.modal-container {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
}

.modal-box {
  width: 50%;
  height: 10rem;
  background: #fff;
  position: absolute;
  top: 30%;
  left: 50;
  padding: 10px;
  padding-top: 50px;
  margin-bottom: 40px;
  display: inline-block;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-animation-name: fadein;
          animation-name: fadein;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.box-input {
  width: 100%;
  margin: 0.5rem 0;
}

.input {
  width: 80%;
  height: 1.5rem;
  margin: 0.5rem;
  background: rgba(3, 176, 195, 0.1);
  border-color: rgba(3, 176, 195, 0.1);
}

.btn-group {
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes fadein {
  from {
    top: -100px;
  }
  to {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
}

@keyframes fadein {
  from {
    top: -100px;
  }
  to {
