modal-container {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
}

.modal-box {
  width: 30%;
  height: 7rem;
  background: #fff;
  position: absolute;
  top: 30%;
  left: 50;
  padding: 10px;
  padding-top: 50px;
  margin-bottom: 40px;
  display: inline-block;
  transform: translate(-50%, -50%);
  animation-name: fadein;
  animation-duration: 0.5s;
}

.modal-box h3 {
  margin: 1rem;
}

.btn-group {
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes fadein {
  from {
    top: -100px;
  }
  to {
    transform: translate(-50%, -50%);
  }
}
